div.profile-details-container {
  display: flex;
  justify-content: space-between;
  background-image: url("https://firebasestorage.googleapis.com/v0/b/fir-login-33c28.appspot.com/o/file%2FIMG_3282.jpg?alt=media&token=acb0eae2-ca91-4b22-81c6-cd2195045d7a");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-clip: content-box;
  height: 20vh;
  position: relative;
}

div.user-profile {
  align-self: flex-end;
  transform: translateY(4em);
  margin: 1em;
}

div.profile-container img {
  width: 7em;
  height: 7em;
  border-radius: 50%;
  border: 0.2em solid var(--header-background);
  background-color: var(--header-background);
  color: var(--text-color);
}

.profile-details-container button {
  height: fit-content;
  padding: 0.5em 0.9em;
  margin: 1em;
}

.profile-info-container {
  margin: 3em auto;
  width: max(90%, 80%);
  background-color: var(--header-background);
  padding: 1em;
}

.profile-about-container ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1em;
}

/* Profile Edit Form */

div.profile-edit-container {
  padding: 1em;
  z-index: 1;
  backdrop-filter: blur(0.3em);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0.1em solid var(--text-color);
  border-radius: 0.3em;
  margin: 2em;
  background-color: var(--secondary-black);
}

.edit-btn-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1em;
}

.edit-btn-container button {
  padding: 1em;
  border-radius: 0.3em;
  border: 0.1em solid var(--dark-rgb);
}

form.profile-edit-form {
  display: flex;
  flex-direction: column;
  gap: 2em;
  -webkit-gap: 2em;
  grid-template-columns: 1fr;
  width: 80%;
  margin: auto;
}

.profile-edit-form label {
  display: flex;
  flex-direction: column;
  padding: 0.3em;
  border: 0.1em solid var(--dark-rgb);
  padding: 1em;
  border-radius: 0.3em;
  background-color: var(--light-rgb);
}

.profile-edit-form label h3 {
  padding: 0.3em;
}

.profile-edit-form input {
  padding: 0.5em;
  border-radius: 0.3em;
}

.profile-edit-form textarea {
  resize: none;
  padding: 1em;
  border-radius: 0.3em;
}
