main {
  position: relative;
}

/* Post */
div.post-container {
  padding: 1em;
  width: max(90%, 80%);
  margin: 2em auto;
  box-shadow: 0 0.5em 0.8em var(--dark-rgb);
  background-color: var(--header-background);
  border-radius: 0.4em;
}

.post-btn {
  padding: 0.4em 0.6em;
  width: 100%;
  color: var(--text-color);
  background-color: transparent;
  border: 0.1em solid var(--text-color);
  border-radius: 1em;
}

/* Modal */
.modal-container {
  backdrop-filter: blur(0.2em);
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100vh;
  top: 0;
}

.modal-wrapper {
  position: relative;
  top: 20%;
  background-color: var(--header-background);
  display: flex;
  flex-direction: column;
  padding: 1em;
  width: max(75%, 70%);
  height: 20em;
  margin: auto;
  gap: 1em;
  -webkit-gap: 1em;
  box-shadow: 0 0.5em 0.8em var(--dark-rgb);
  border-radius: 0.3em;
}

.modal-container textarea {
  padding: 1em;
  resize: none;
}

.modal-container textarea::-webkit-scrollbar {
  display: none;
}

.modal-wrapper button {
  align-self: flex-end;
  padding: 0.5em 0.8em;
  border-radius: 0.3em;
}

.close-modal {
  background-color: var(--background-color);
  color: var(--text-color);
}

.send-btn {
  background-color: blue;
  color: var(--header-background);
}

/* Post Content */
div.post-content {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1em;
  -webkit-gap: 1em;
  width: 80%;
  margin: auto;
}

.post-card {
  padding: 1em;
  box-shadow: 0 0.3em 0.5em var(--dark-rgb);
  background-color: var(--header-background);
}

.post-head {
  display: flex;
  gap: 0.4em;
  -webkit-gap: 0.4em;
  place-items: center;
}

.post-body {
  padding: 1em;
}

.post-interaction {
  display: flex;
  justify-content: space-between;
  padding: 1em;
}
